import React, { Component } from "react";

class ProjectCard extends Component {
    render() {
        return <div className="project-container">

            <div className="item" style={{ backgroundImage: `url(${this.props.image})` }} />

            <div>
                <h1 className="padding">{this.props.name}</h1>
                <div className="tags padding">
                    {this.props.tag.map(tags => <div className="tag">{tags}</div>)}
                </div>
                <p className="project-text padding">
                    {this.props.describe}
                </p>
                <div className="tags padding">
                    <a href={this.props.access} target="blank"><div className="tag link">Acesso</div></a>
                </div>
            </div>
        </div>
    }
}
export default ProjectCard;