import React, { Component } from "react";

import ProjectCard from "./ProjectCard";

import cortaiImg from "../_assets/img/Cortai-project.png";
import autoHouseImg from "../_assets/img/autohouse-project.png";
import cMemoriaImg from "../_assets/img/Centro-memoria-project.png";
import maisIfmgImg from "../_assets/img/mais-ifmg-project.png";

class Projects extends Component {
    constructor(props) {
        super(props)
        this.state = {
            project: [
                {
                    name: "Cortaí",
                    tag: ["Flutter", "AWS"],
                    describe: ' Aplicativo desenvolvido para satisfazer a demanda de agendamento de cortes de cabelo durante a pândemia, no qual os clientes podem realizar seus agendamentos diretamente pelo aplicativo, escolhendo data/hora e forma de pagamento, além disso o prestador do serviço conta com um painel administrativo dentro do próprio aplicativo para fazer o gerenciamento dos agendamentos',
                    image : cortaiImg,
                    access : "https://play.google.com/store/apps/details?id=com.nfsoft.cortai"
                },
                {
                    name: "+IFMG",
                    tag: ["Flutter", "Firebase"],
                    describe: ' Desenvolvimento de aplicação móvel para a exibição de noticias da instituição e acesso a programa de cursos online',
                    image : maisIfmgImg,
                    access : "https://play.google.com/store/apps/details?id=com.ifmg.maisifmg"
                },
                {
                    name: "AutoHouse",
                    tag: ["Flutter", "Firebase", "Arduino/ESP32"],
                    describe: ' Aplicativo desenvolvido como plataforma de acionamento de cargas remotas de um circutio integrado do qual faz o uso de relés e outros dispositivos eletrônicos, sendo um projeto de Pesquisa do IFMG',
                    image : autoHouseImg,
                    access : ""
                },
                {
                    name: "Centro de Memória",
                    tag: ["Wordpress", "PHP"],
                    describe: "Site desenvolvido para o IFMG como uma iniciativa para distruibuição de conteúdos sobre a História da instituição, servindo como um espaço de interação entre a comunidade interna e externa da instituição",
                    image : cMemoriaImg,
                    access : "http://memoria.ifmg.edu.br/"
                }
            ]
        }
    }
    render(){
        return(
            <div className="grid-template-columns" >
                {this.state.project.map(item => {
                    return(
                        <ProjectCard 
                        name = {item.name}
                        tag = {item.tag}
                        describe = {item.describe}
                        image = {item.image}
                        access = {item.access}
                        />
                    )
                })}
            </div>
        )
    }
}

export default Projects;